* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.LogInBox {
  min-width: 30rem;
  max-width: 90%;
  min-height: 45rem;
  margin: 5rem 0 8rem 5rem;
  padding: 15px;
  text-align: left;
  position: relative;
}

@media (max-width: 600px) {
  .LogInBox {
    margin: 5rem auto 8rem auto;
  }
}

.LogInBox::after {
  content: "";
  position: absolute;
  inset: 0;
  background-color: rgb(189, 182, 182);
  z-index: -2;
}

.LogInBox::before {
  content: "";
  position: absolute;
  inset: 5px;
  background-color: rgb(245, 255, 250);
  border-radius: 8px;
  z-index: -1;
}

label {
  font-weight: bold;
}

@media (max-width: 600px) {
  label {
    font-size: 3rem;
  }
}

.LogInBox input {
  display: block;
  margin: 20px auto 5px auto;
  height: 30px;
  border-radius: 5px;
  padding-left: 10px;
  font-size: 20px;
  border: 1px solid black;
  outline: none;
}

@media (max-width: 850px) {
  .LogInBox input {
    width: 90%;
  }
}

.LogInBox .error {
  color: red;
  font-size: 14px;
  text-align: center;
  font-weight: normal;
}

.LogInBox button {
  width: 70%;
  height: 40px;
  font-weight: bold;
  color: white;
  border: none;
  display: block;
  margin: 25px auto 0 auto;
  border-radius: 7px;
  font-size: 25px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  z-index: 1;
}

.LogInBox button::before {
  content: "";
  position: absolute;
  inset: 0;
  border-radius: 7px;
  background-color: green;
  z-index: -2;
}

.LogInBox button::after {
  content: "";
  position: absolute;
  inset: 0;
  border-radius: 7px;
  background-color: rgb(18, 197, 18);
  -webkit-transform: translate(-100%);
          transform: translate(-100%);
  transition: -webkit-transform 0.3s;
  transition: transform 0.3s;
  transition: transform 0.3s, -webkit-transform 0.3s;
  opacity: 0.5;
  z-index: -1;
}

.LogInBox button:hover::after {
  -webkit-transform: translate(0);
          transform: translate(0);
}

.LogInBox .code {
  text-align: center;
  font-weight: bold;
  letter-spacing: 5px;
  margin-top: 20px;
  margin-bottom: 5px;
  font-size: 25px;
}

@media (max-width: 600px) {
  .LogInBox .code {
    font-size: 7rem;
  }
}

.LogInBox p {
  text-align: center;
}

@media (max-width: 600px) {
  .LogInBox p {
    font-size: 3rem;
  }
}

header h2 {
  font-size: 6rem;
  z-index: 5;
  color: black;
  padding-top: 2rem;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  -webkit-animation: from-left 1s forwards;
          animation: from-left 1s forwards;
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
}

@-webkit-keyframes from-left {
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

@keyframes from-left {
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

.slide-one {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
  -webkit-animation: slideOne 10s forwards;
          animation: slideOne 10s forwards;
  -webkit-animation-delay: 10s;
          animation-delay: 10s;
}

.slide-one * {
  width: 65%; /*because absolute element has this width n I want center this*/
  text-align: center;
}

.slide-one p {
  font-size: 2.8rem;
  margin-top: 1.8rem;
  -webkit-transform: translateY(100%);
          transform: translateY(100%);
  opacity: 0;
  -webkit-animation: from-bottom 1s forwards;
          animation: from-bottom 1s forwards;
  -webkit-animation-delay: 2.5s;
          animation-delay: 2.5s;
}

@media (max-width: 600px) {
  .slide-one p {
    font-size: 1.8rem;
  }
}

@-webkit-keyframes from-bottom {
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
  }
}

@keyframes from-bottom {
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
  }
}

.slide-one p:nth-child(3) {
  color: green;
  -webkit-animation-delay: 3.5s;
          animation-delay: 3.5s;
}

.slide-one p:nth-child(4) {
  padding-top: 2rem;
  font-size: 2.2rem;
  -webkit-animation-delay: 4.5s;
          animation-delay: 4.5s;
}

.slide-one::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 65%;
  background-color: mintcream;
  opacity: 0.55;
  -webkit-clip-path: polygon(0 0, 100% 0, 90% 100%, 0% 100%);
  clip-path: polygon(0 0, 100% 0, 90% 100%, 0% 100%);
  z-index: -1;
}

.slide-one::before {
  content: "";
  position: absolute;
  inset: 0;
  background-image: url(/BankApplication/static/media/slideone.c9cae076.jpg);
  background-size: cover;
  background-position: center;
  opacity: 1;
  z-index: -2;
  background-position: 0% 10%;
}

.slide-two {
  width: 100%;
  height: 100%;
  position: absolute;
  inset: 0;
  overflow: hidden;
  background-image: url(/BankApplication/static/media/slidetwo.617f3804.jpg);
  background-size: cover;
  background-position: center;
  z-index: 5;
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
  -webkit-animation: fromRight 2s forwards;
