  background: rgb(236, 4, 6);
  background: linear-gradient(
    51deg,
    rgba(236, 4, 6, 1) 0%,
    rgba(249, 247, 245, 1) 0%,
    rgba(243, 239, 230, 1) 81%,
    rgba(221, 212, 179, 1) 100%,
    rgba(223, 214, 181, 1) 100%,
    rgba(210, 201, 168, 1) 100%,
    rgba(164, 155, 124, 1) 100%
