  flex-grow: 1;
}

.circle {
  background-color: white;
  width: 10rem;
  height: 10rem;
  border-radius: 50%;
  box-shadow: 0px 0px 5px 2px rgba(0, 0, 0, 0.3);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 5rem;
}

.block-one {
  top: 70%;
  opacity: 0;
  -webkit-animation: fly 10s infinite;
          animation: fly 10s infinite;
  -webkit-animation-delay: 2s;
          animation-delay: 2s;
  -webkit-transform: scale(0.85);
          transform: scale(0.85);
}

.block-two {
  top: 70%;
  opacity: 0;
  -webkit-animation: fly 10s infinite;
          animation: fly 10s infinite;
  -webkit-animation-delay: 5s;
          animation-delay: 5s;
  -webkit-transform: scale(0.85);
          transform: scale(0.85);
}

.block-two .circle {
  -webkit-transform: rotate(30deg);
          transform: rotate(30deg);
}

.block-three {
  top: 70%;
  opacity: 0;
  -webkit-animation: fly 10s infinite;
          animation: fly 10s infinite;
  -webkit-animation-delay: 8s;
          animation-delay: 8s;
  -webkit-transform: scale(0.85);
          transform: scale(0.85);
}

@-webkit-keyframes fly {
  15% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
  }

  50% {
    top: 5%;
    opacity: 1;
  }

  60% {
    opacity: 0;
  }

  100% {
    top: 70%;
    opacity: 0;
  }
}

@keyframes fly {
  15% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
  }

  50% {
    top: 5%;
    opacity: 1;
  }

  60% {
    opacity: 0;
  }

  100% {
    top: 70%;
    opacity: 0;
  }
}

